body {
  height: 100%;
  margin: 0;
}

body {
  background: linear-gradient(
    to right,
    rgba(0, 0, 0, 0.85),
    rgba(0, 0, 128, 0.7)
  );

  font-family: Verdana;
  margin: 0;
  color: white;
  display: flex;
  flex-direction: column;
}

a {
  color: white;
  text-decoration: none;
  padding: 10px;
}

p,
h2 {
  color: white;
  line-height: 1.5;
}

header {
  background-color: navy;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 5px;
  padding: 10px 50px;
  background-color: navy;
  color: white;
}

.title {
  margin: 0;
  font-size: 32px;
}

.navbar {
  display: flex;
  justify-content: flex-end;
  gap: 70px;
}

.navbar_element:hover {
  color: #cecece;
  border: solid 1px white;
  border-radius: 5px;
  padding: 8px;
  background-color: rgba(255, 255, 255, 0.1);
  transition: background-color 0.3s ease;
}

.main-container {
  flex: 1;
  display: flex;
  flex-direction: column;
  padding: 20px;
  height: auto;
}

.background-image {
  background: linear-gradient(
      to right,
      rgba(0, 0, 0, 0.85),
      rgba(0, 0, 128, 0.7)
    ),
    url("https://images.pexels.com/photos/1153369/pexels-photo-1153369.jpeg?cs=srgb&amp;dl=pexels-mastercowley-1153369.jpg&amp;fm=jpg");
  background-size: cover;
  background-position: center;
  color: white;
  padding: 40px;
  border-radius: 8px;
}

.about-background-image {
