  background-size: cover;
  background-position: center;
  color: white;
  padding: 40px;
  border-radius: 8px;
}

.join-background-image {
  background: linear-gradient(
      to right,
      rgba(0, 0, 0, 0.85),
      rgba(0, 0, 128, 0.7)
    ),
    url("https://images.pexels.com/photos/1000445/pexels-photo-1000445.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=1");
  background-size: cover;
  background-position: center;
  color: white;
  padding: 40px;
  border-radius: 8px;
}

.login-background-image {
  background: linear-gradient(
      to right,
      rgba(0, 0, 0, 0.85),
      rgba(0, 0, 128, 0.7)
    ),
    url("https://images.pexels.com/photos/1092730/pexels-photo-1092730.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=1");
  background-size: cover;
  background-position: center;
  color: white;
  padding: 40px;
  border-radius: 8px;
}


.user-form {
  flex: 1;
  display: flex;
  justify-content: space-around;
  padding: 20px;
  height: auto;
}


#sign-up,
#login {
  text-align: center;
  border: solid 1px white;
  width: 50%;
}

#sign-up input[type="text"],
#sign-up input[type="password"],
#login input[type="text"],
#login input[type="password"] {
  text-align: left;
  padding: 10px;
  border-radius: 5px;
  box-sizing: border-box; /* makes sure padding doesn't effect txt field size*/
  width: 40%;
}

#sign-up input[type="text"]:focus,
#sign-up input[type="password"]:focus,
#login input[type="text"]:focus,
#login input[type="password"]:focus {
  border-color: navy;
  outline: none;
}

#sign-up input::placeholder,
#login input::placeholder {
  text-align: left; /* Placeholder text aligned to the left */
}

.submit-btn {
  background-color: navy;
  color: white;
  padding: 15px 65px;
  border-radius: 10px;
  cursor: pointer;
}

.submit-btn:hover {
  background-color: darkblue;
}

#back-to-sign-up a,
#back-to-sign-up u {
  color: white;
}

#back-to-sign-up a:hover,
#back-to-sign-up u:hover {
  color: navy;
}

table {
  width: 100%;
  border-collapse: collapse;
  margin: 20px 0;
  color: white;
  background-color: rgba(255, 255, 255, 0.1);
  border: 1px solid white;
  border-radius: 5px;
  overflow: hidden;
}

table th,
table td {
  padding: 15px;
  text-align: left;
  border-bottom: 1px solid rgba(255, 255, 255, 0.3);
}

table th {
  background-color: rgba(0, 0, 128, 0.8);
}

table tr:hover {
  background-color: rgba(255, 255, 255, 0.05);
  transition: background-color 0.3s ease;
}

.faq-section {
  margin-top: 20px;
}

.faq-question {
  background-color: rgba(0, 0, 128, 0.8);
  color: white;
  padding: 15px;
  cursor: pointer;
  border: none;
  outline: none;
  width: 100%;
  text-align: left;
  font-size: 16px;
  position: relative;
  transition: background-color 0.3s ease;
}

.faq-question::after {
  content: "\\25BC";
  font-size: 12px;
  position: absolute;
  right: 15px;
  transition: transform 0.3s ease;
}

.faq-question.active::after {
  transform: rotate(-180deg);
}

.faq-question:hover {
  background-color: darkblue;
}

.faq-answer {
  max-height: 0; /* Start collapsed */
  padding: 0 15px;
  overflow: hidden;
  color: white;
  background-color: rgba(0, 0, 0, 0.7);
  transition: max-height 0.3s ease, padding 0.3s ease;
}

.faq-answer.visible {
  max-height: 500px; /* Increase max-height */
  padding: 15px; /* Add padding when expanded */
}

.navbar_element {
    padding: 10px;
    text-align: center;
}

.hamburger-menu {
    display: none;
    font-size: 30px;
    cursor: pointer;
    color: white;
}

.navbar {
    display: flex;
    justify-content: flex-end;
    gap: 70px;
}

#mobileMenu {
    display: none; 
    flex-direction: column;
    gap: 10px;
    background-color: navy;
    padding: 10px;
}

@media (max-width: 768px) {
    .hamburger-menu {
        display: block;
    }
  
    #desktopMenu {
        display: none;
    }
  
    #mobileMenu.active {
        display: flex;
    }
}

/* Footer CSS */
#footer {
  background: linear-gradient(to right, rgba(0, 0, 128, 0.8), rgba(0, 0, 64, 0.8));
  color: white;
  padding: 5px;
  font-size: 13px;
}

.footer-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  gap: 20px;
  max-width: 1200px;
  margin: 0 auto;
}

.footer-container > div {
  flex: 1 1 200px;
  text-align: left;
}

.footer-links ul {
  list-style: none;
  padding: 0;
}

.footer-links ul li {
  margin: 5px 0;
}

.footer-links ul li a {
  color: white;
  text-decoration: none;
  font-size: 14px;
}

.footer-links ul li a:hover {
  text-decoration: underline;
  color: #cecece;
}

.footer-social a {
  color: white;
  font-size: 20px;
  margin-right: 10px;
  text-decoration: none;
}

.footer-social a:hover {
  color: #cecece;
}

.footer-bottom {
  text-align: center;
  margin-top: 10px;
  border-top: 1px solid rgba(255, 255, 255, 0.2);
  padding-top: 5px;
  font-size: 11px;
}

.newsletter-signup {
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin-top: 15px;
}

.newsletter-signup input,
.newsletter-signup button {
  padding: 8px;
  border: none;
  border-radius: 5px;
}

.newsletter-signup button {
  background-color: darkblue;
  color: white;
  cursor: pointer;
}

.newsletter-signup button:hover {
  background-color: navy;
}

