  display: flex;
  justify-content: center;
  gap: 70px;
}

.navbar a {
  color: white;
  text-decoration: none;
  padding: 10px;
}

.navbar a:hover {
  color: #cecece;
  border: solid 1px white;
  border-radius: 5px;
  padding: 8px;
  background-color: rgba(255, 255, 255, 0.1);
  transition: background-color 0.3s ease;
}

.userProfile-section {
  display: flex;
  justify-content: center;
