  flex: 1;
  padding: 20px;
}

.profile-container {
  background-color: rgba(0, 0, 0, 0.7);
  padding: 30px;
  border-radius: 8px;
  width: 100%;
  max-width: 800px;
  text-align: center;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
}

.profile-container h2 {
  font-size: 28px;
  margin-bottom: 20px;
}

.user-details {
  margin-bottom: 30px;
}

.user-details p {
  font-size: 18px;
  line-height: 1.8;
}

.user-details strong {
  color: #36A9AE;
}

.actions {
  display: flex;
  justify-content: center;
}

.button-25 {
  background-color: #36A9AE;
  background-image: linear-gradient(#37ADB2, #329CA0);
